:root { --polaris-version-number: '11.26.0'; }

html[class~="Polaris-Summer-Editions-2023"] { --pc-app-provider-shadow-bevel-content: ''; --pc-app-provider-shadow-bevel-border: none; }

html[class~="Polaris-SE23-Shadow-Bevel-Opt-Out"] { --pc-app-provider-shadow-bevel-content: none; --pc-app-provider-shadow-bevel-border: var(--p-border-width-025) solid
